


















































































































































































































.fixed-header {
    position: fixed;
    height: 0.7rem;
    left: 0;
    top: 0;
    right: 0;
    z-index: 9;
    background: #fff;
    -webkit-transition: width 0.28s;
    transition: width 0.28s;

    .margin-right {
        margin-right: 15px !important;
    }
}

.gray-color {
    color: #909CB1;

}

.point-text {
    position: absolute;
    top: -500px;
    left: 0;
}


.navbar {
    height: 0.7rem;
    padding: 0px 0.1rem 0 0.17rem;
    display: flex;
    justify-content: space-between;
}

.logo {
    display: flex;
    padding-left: 0.06rem;
    width: 2.83rem;
    box-sizing: border-box;
}

.logo .logo-img {
    width: 1.2rem;
    height: 0.6rem;
    margin-top: 3px;
    vertical-align: top;
    margin-right: 0.1rem;
}

.logo span {
    font-size: 0.18rem;
    line-height: 0.74rem;
    display: inline-block;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: bold;
    color: #000000;
}

.search-box {
    display: flex;
    flex: 1;
    align-items: center;
    font-size: 0.18rem;
    height: 0.7rem;
    box-sizing: border-box;
}

.search-box input {
    outline: none;
    border: none;
    height: 0.25rem;
    max-width: 2rem;
    overflow: hidden;
}

.right-menu {
    display: flex;
    align-items: center;
    height: 100%;

    /deep/ .el-select {
        width: 100%;
    }

    /deep/ .el-dropdown-link i {
        font-size: 16px;
        transition: all 0.6s;

        &:hover {
            transform: rotate(100deg);
            color: #2E4AF7 !important;
        }
    }
}

.right-menu-item {
    display: flex;
    align-items: center;
    font-size: 0.14rem;
    margin: 0 0.15rem;
    height: 100%;
    line-height: 0.56rem;
}

.right-menu-item:last-child {
    margin-right: 0;
}

.head-img {
    display: flex;
    align-items: center;
    margin-right: 0;
    margin-left: 0.15rem;
    width: 0.34rem;
    height: 0.34rem;
    background: url("../../../assets/head.jpeg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50%;
    border-radius: 50%;
}

.el-icon-search {
    font-size: 0.2rem;
    margin-right: 0.13rem;
    vertical-align: middle;
}

.el-icon-message-solid {
    font-size: 0.21rem;
}

.crew {
    font-size: 0.16rem;
    color: #2e4af7;
}

.select-names {
    margin-right: 10px;
    display: flex;
    align-items: center;
    font-size: 0.14rem;
    height: 100%;
    line-height: 0.56rem;

    /deep/ .el-input__inner {
        color: #909CB1;

    }
}

.select-names {
    ::v-deep .el-input--suffix .el-input__inner {
        text-align: right;

        border: none;
        outline: none;
    }
}

.el-dropdown-link {
    cursor: pointer;
}

.logout {
    font-size: 14px !important;

    span {
        margin-left: 10px;
    }
}
