







































































.slider-aside-out {
    position: fixed;
    left: 0;
    top: 0.7rem;
    bottom: 0;
    width: 2.7rem;
    height: calc(100% - 0.7rem);
    font-size: 0;
    overflow: hidden;

    .box {
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;

        .slider-aside {
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            right: -10px;
            -webkit-transition: width 0.28s;
            transition: width 0.28s;
            font-size: 0;
            overflow-y: auto;
            padding-bottom: 20px;

        }
    }
}



.unit-box {
    padding: 0 0.28rem;
}

.unit-box>div {
    width: 2.16rem;
    height: 0.96rem;
    background: rgba(46, 74, 247, 0.12);
    border-radius: 0.06rem;
    padding: 0.17rem 0.32rem 0 0.17rem;
    box-sizing: border-box;
}

.unit-box>div div {
    height: 0.31rem;
    line-height: 0.31rem;
    text-align: left;
    color: #333333;
    font-size: 0.16rem;
}
