






























.app-warrper {
    position: relative;
    width: 100%;
    display: flex;
}

.hideSidebar .slider-aside {
    width: 0.6rem !important;
}

.main-container {
    width: calc(100% - 2.7rem);
    min-height: calc(100vh - 0.7rem);
    margin-top: 0.7rem;
    margin-left: 2.7rem;
    justify-items: center;
    box-sizing: border-box;
    background: #f5f6fa;
}

.app-container {
    box-sizing: border-box;
    display: flex;
    padding: 0.32rem;
    justify-content: center;
    width: 100%;
    // height: calc(100% - 0.7rem);
    overflow: hidden;
    border-radius: 6px;
}
