









































































































.form {
    .li {

        /deep/ .el-form-item {
            margin-bottom: 0;
            width: 100%;
        }

        /deep/ .el-input__inner {
            width: 2.2rem;
        }

        display: flex;
        margin-bottom: 22px;

        .item {
            width: 100%;
            display: flex;
            line-height: 32px;
            height: 32;

            .label {
                font-family: "PFC";
                min-width: 1rem;
                margin-right: 2px;
                color: #333;
                text-align: right;
            }

            .box {
                width: 100%;
                border-radius: 4px;
                color: #666;
                box-sizing: border-box;
            }

            .rt {
                flex: 1;
            }

            .btns {
                width: 100%;
                display: flex;
                justify-content: flex-end;

            }
        }
    }
}
